.App {
  text-align: center;
  /* align-items: center; */
  width: 100%;
  background-color: #FFA9A9;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #FFA9A9;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 50%;
  padding-left: 25%;
}

.games {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 50%;
  padding-left: 25%;
}

.login {
  display: block;
  flex-flow: row;
  padding-left: 10%;
  padding-right: 10%;
  place-content: space-between;
}

.sub-login {
  padding-left: 15%;
  padding-right: 15%;
  display: inline-grid;
  place-content: space-between;
}

.stats {
  display: flex;
  flex-flow: row;
  padding-left: 5%;
  padding-right: 5%;
  place-content: space-between;
  text-align: justify;
  background-color: #FFA9A9;
}

.table-header {
  background-color: #FFA9A9;
}

.btn {
  background-color: #F03737;
  border: none;
  color: white;
  padding: 8px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition: 0.3s;
  cursor: pointer;
  margin: 10px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border-radius: 10px;
  margin: 20px;
  padding: 20px;
}

.card:hover {
  box-shadow: 0 8px 32px 0 rgba(0,0,0,0.2);
}

.btn:hover {
  background-color: gray;
  transition: 0.3s;
  cursor: pointer;
}

.btn:disabled {
  background-color: gray;
  cursor: not-allowed;
}

* {
  margin-top: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
